/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import actionRecipesData from './data-bb-action-recipes-http';

const actionRecipesDataModuleKey = 'data-bb-action-recipes-http-ng';

export const actionRecipesDataKey = 'data-bb-action-recipes-http-ng:actionRecipesData';

export default ng
  .module(actionRecipesDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(actionRecipesDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       actionRecipesData(config),
      ],
    };
  }])
  .name;



